import { Component, OnInit, Inject} from '@angular/core';
import { SessionService } from '../../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-edit-certifications',
  templateUrl: './edit-certifications.component.html',
  styleUrls: ['./edit-certifications.component.css']
})
export class EditCertificationsComponent implements OnInit {
  certificationForm = {
    certificationId: '',
    name: '',
    entity: '',
    year:''
  }
  error: any;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private session: SessionService, public certificationEditRef: MatDialogRef<EditCertificationsComponent>) { }

  ngOnInit() {
    this.certificationForm.certificationId = this.data.certification;
    this.session.getCertification(this.data.certification)
    .subscribe(certification => {
      this.certificationForm.name = certification[0].name;
      this.certificationForm.entity = certification[0].entity;
      this.certificationForm.year = certification[0].year;
    })
  }

  editCertification() {
    this.session.editCertification(this.certificationForm)
      .subscribe(experience => {
          this.certificationEditRef.close(experience)
      })
  }

}



