import { Component, OnInit, Inject} from '@angular/core';
import { SessionService } from '../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-edit-profile-dialog',
  templateUrl: './edit-profile-dialog.component.html',
  styleUrls: ['./edit-profile-dialog.component.css']
})
export class EditProfileDialogComponent implements OnInit {
user;
formInfo = {
  id: '',
  name: '',
  lastName: '',
  city: '',
  country: '',
  phone: '',
  email: '',
  linkedin: '',
  web: '',
  profession: ''
};
error;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,  private session: SessionService, public userEditRef: MatDialogRef<EditProfileDialogComponent>) { }

  ngOnInit() {
    this.session.userLoggedIn()
    .subscribe(user => {
      this.formInfo.name = user.name;
      this.formInfo.lastName = user.lastName;
      this.formInfo.city = user.city;
      this.formInfo.country = user.country;
      this.formInfo.phone = user.phone;
      this.formInfo.email = user.email;
      this.formInfo.linkedin = user.linkedin;
      this.formInfo.profession = user.profession;
    })
  }

  editar() {
    if (this.formInfo.name == '' ||
    this.formInfo.lastName == '' ||
    this.formInfo.city == '' ||
    this.formInfo.country == '' ||
    this.formInfo.phone == '' ||
    this.formInfo.email == '' ||
    this.formInfo.linkedin == '' ||
    this.formInfo.profession == '') {
      this.error = "Debes completar todos los campos!"
} else {
  this.session.editUser(this.formInfo)
  .subscribe(user => {
      this.userEditRef.close(user)
  })
}
    
  }
}



