import { Component, OnInit, Input } from '@angular/core';
import { SessionService } from '../../session.service';
import { ActivatedRoute, Router } from '@angular/router'
declare var Materialize: any;
import {MatDialog} from '@angular/material';
import {AddAcademicsDialogComponent} from '../add-academics-dialog/add-academics-dialog.component'
import {EditAcademicsDialogComponent} from '../edit-academics-dialog/edit-academics-dialog.component'

@Component({
  selector: 'app-academics',
  templateUrl: './academics.component.html',
  styleUrls: ['./academics.component.css']
})
export class AcademicsComponent implements OnInit {
  @Input() academics: any;
  birthdate;
  formInfo = {
    institute: '',
    userId: '',
    startDate: '',
    endDate: '',
    degreeTitle: '',
    degreeLevel: '',
    city: '',
    country: '',
    description: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = false;
  showForm:boolean = false;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.","Mar.","Abr.","May.","Jun.","Jul.","Ago.","Sep.","Oct.", "Nov.","Dic."]
  selectOptionsYears = ["2017", "2016","2015","2014","2013","2012","2011","2010","2009","2008", "2007","2006", "2005","2004","2003","2002","2001","2000"]

      constructor(private session: SessionService, private route: ActivatedRoute, private router: Router, public dialog: MatDialog) {
          // this.birthdate = new Date();
      }
      ngOnInit() {
        this.route.params
          .subscribe((params) => {
            this.formInfo.userId = params['id'];
            this.session.getAcademics(this.formInfo.userId)
            .subscribe((academics) => {
              this.academics = academics;
            });
        });

      }



      addAcademics() {
        let dialogRef = this.dialog.open(AddAcademicsDialogComponent, {
          height: 'auto',
          width: '600px',

        });
        dialogRef.afterClosed().subscribe(result => {
          if (result !== undefined) {
            this.academics.unshift(result)
          }
        });
      }

      errorCb(err) {
        this.error = err;
        this.user = null;
      }


      remove(id, event){
        this.session.deleteAcademics(id)
          .subscribe(
            (academics) => {
              this.academics = this.academics.filter(e => e._id !== id);
            },
            (err) => this.errorCb(err)
          );
      }

      edit(id) {
        let editDialogRef = this.dialog.open(EditAcademicsDialogComponent, {
          height: 'auto',
          width: '600px',
          data: {academic: id}
        });
        editDialogRef.afterClosed().subscribe(result => {
          if (result !== undefined) {
            console.log(result)
            this.academics = this.academics.filter(e => e._id !== result._id);
            this.academics.unshift(result);
          }
        });
      }

}



