import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-select-courses-hobbies',
  templateUrl: './select-courses-hobbies.component.html',
  styleUrls: ['./select-courses-hobbies.component.css']
})
export class SelectCoursesHobbiesComponent implements OnInit {
  @Input() courses: any;
  @Output() onCourses = new EventEmitter<any>();
  coursesSelected = [];
  constructor() {

   }

  ngOnInit() {

  }
  toggleCourses(e, courses) {
    console.log(e.target.children[0])
    if(!e.target.children[0].classList.contains("active")) {
      e.target.children[0].classList.add("active");
      this.coursesSelected.push(courses);
    } else {
      e.target.children[0].classList.remove("active");
      this.coursesSelected = this.coursesSelected.filter(e => e._id !== courses._id);
    }
    this.onCourses.emit(this.coursesSelected);
  }

}



