import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-design-2',
  templateUrl: './design-2.component.html',
  styleUrls: ['./design-2.component.css']
})
export class Design2Component implements OnInit {
  @Input() currentColor:any;
  @Input() cv:any;
  constructor() { }

  ngOnInit() {
    this.currentColor = this.cv.color;
  }

}



