import { Component, OnInit } from '@angular/core';
import { SessionService } from '../../session.service';
import { MatDialog, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-add-experience-dialog',
  templateUrl: './add-experience-dialog.component.html',
  styleUrls: ['./add-experience-dialog.component.css']
})
export class AddExperienceDialogComponent implements OnInit {
  formInfo = {
    company: '',
    userId: '',
    startDate: '',
    endDate: '',
    jobPosition: '',
    city: '',
    country: '',
    jobDescription: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = true;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.","Mar.","Abr.","May.","Jun.","Jul.","Ago.","Sep.","Oct.", "Nov.","Dic."]
  selectOptionsYears = ["2017", "2016","2015","2014","2013","2012","2011","2010","2009","2008", "2007","2006", "2005","2004","2003","2002","2001","2000"]
  constructor(private session: SessionService, public experienceAddRef: MatDialogRef<AddExperienceDialogComponent>) { }

  ngOnInit() {
  }
  addExperience() {
    this.formInfo.startDate = this.startMonth+" '"+this.startYear.slice(2,4);
    this.endMonth !== undefined ? this.formInfo.endDate = this.endMonth+" '"+this.endYear.slice(2,4) : this.formInfo.endDate = '' ;
    this.session.addExperience(this.formInfo)
      .subscribe(
        (experience) => this.successCb(experience),
        (err) => this.errorCb(err)
      );

  }

  errorCb(err) {
    this.error = err;
    this.user = null;
  }

  successCb(experience) {
    this.error = null;
    this.experienceAddRef.close(experience)
  }

  toggleEndDate() {
    this.endDate = !this.endDate;
  }

}



