import { Component, OnInit, Inject } from '@angular/core';
import { SessionService } from '../../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-edit-skills',
  templateUrl: './edit-skills.component.html',
  styleUrls: ['./edit-skills.component.css']
})
export class EditSkillsComponent implements OnInit {
  formInfo = {
    skillId: '',
    skill: '',
    level: 50
  };
  error:any;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private session: SessionService, public skillEditRef: MatDialogRef<EditSkillsComponent>) { }

  ngOnInit() {
    this.formInfo.skillId = this.data.skill;
    this.session.getSkill(this.data.skill)
    .subscribe(skill => {
      this.formInfo.skill = skill[0].skill;
      this.formInfo.level = skill[0].level;
    })
  }

  editSkill() {
    this.session.editSkill(this.formInfo)
      .subscribe(
        (skill) => this.successCb(skill),
        (err) => this.errorCb(err)
      );
  }

  errorCb(err) {
    this.error = err;
  }

  successCb(skill) {
    this.skillEditRef.close(skill)
    this.error = null;
  }

}



