import { Component, OnInit, Input } from '@angular/core';
import { SessionService } from '../../session.service'
import { ActivatedRoute } from '@angular/router'
import {MatDialog} from '@angular/material';
import {AddCertificationsComponent} from '../add-certifications/add-certifications.component'
import {EditCertificationsComponent} from '../edit-certifications/edit-certifications.component'

@Component({
  selector: 'app-certifications',
  templateUrl: './extras.component.html',
  styleUrls: ['./extras.component.css']
})
export class CertificationsComponent implements OnInit {
  @Input() certifications: any;
  certificationForm = {
    userId: '',
    name: '',
    entity: '',
    year:''
  }
  error: any;

  constructor(private session: SessionService, private route: ActivatedRoute, public dialog: MatDialog) { }

  ngOnInit() {

  }


  addCertification() {
    let dialogRef = this.dialog.open(AddCertificationsComponent, {
      height: 'auto',
      width: '300px',

    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.certifications.unshift(result)
      }
    });
  }

  editCertification(id) {
    let dialogRef = this.dialog.open(EditCertificationsComponent, {
      height: 'auto',
      width: '300px',
      data: {certification: id}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.certifications = this.certifications.filter(e => e._id !== result._id);
        this.certifications.unshift(result)
      }
    });
  }

  errorCb(err) {
    this.error = err;
  }

  removeCertification(id, event){
    this.session.deleteCertification(id)
      .subscribe(
        (certification) => {
          this.certifications = this.certifications.filter(e => e._id !== id);
        },
        (err) => this.errorCb(err)
      );
  }


}



