import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { SessionService } from '../session.service';
import {MatDialog} from '@angular/material';
import {AddSkillsComponent} from './add-skills/add-skills.component'
import {EditSkillsComponent} from './edit-skills/edit-skills.component'

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})


export class SkillsComponent implements OnInit {
  @Input() skills: any;
  formInfo = {
    skill: '',
    userId: '',
    level: 50
  };
  error:any;

  constructor(private session: SessionService, private route: ActivatedRoute, private router: Router, public dialog: MatDialog) { }

  ngOnInit() {
    this.route.params
      .subscribe((params) => {
        this.formInfo.userId = params['id'];
        this.session.getSkills(this.formInfo.userId)
        .subscribe((skills) => {
          this.skills = skills;
        });
    });
  }

  addSkill() {
    let dialogRef = this.dialog.open(AddSkillsComponent, {
      height: 'auto',
      width: '300px',

    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.skills.unshift(result)
      }
    });
  }

  errorCb(err) {
    this.error = err;
    this.skills = null;
  }

  successCb(skill) {
    this.skills.unshift(skill);
    this.error = null;
  }

  removeSkill(id, event){
    this.session.deleteSkill(id)
      .subscribe(
        (skill) => {
          this.skills = this.skills.filter(e => e._id !== id);
        },
        (err) => this.errorCb(err)
      );
  }
  editSkill(id) {
    let editDialogRef = this.dialog.open(EditSkillsComponent, {
      height: 'auto',
      width: '300px',
      data: {skill: id}
    });
    editDialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.skills = this.skills.filter(e => e._id !== result._id);
        this.skills.unshift(result)
      }
    });
  }

}



