import { Component, OnInit } from '@angular/core';
import { SessionService } from '../session.service';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { EditProfileDialogComponent  } from '../edit-profile-dialog/edit-profile-dialog.component'
import { StepperBuilderComponent } from '../stepper-builder/stepper-builder.component';
import { FileUploader} from 'ng2-file-upload';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  uploader: FileUploader = new FileUploader({
    url: `http://localhost:3000/upload-photo`
  });
  user: any;
  experiences: any;
  academics: any;
  skills: any;
  hobbies: any;
  certifications: any;
  dashboardContainer: boolean = true;
  experienceContainer: boolean = false;
  academicContainer: boolean = false;
  skillContainer: boolean = false;
  hobbieContainer: boolean = false;
  certificateContainer: boolean = false;
  dashboard;
  personalInfo;
  isSkillSelected: boolean = false;
  feedback;
  curriculums;

  constructor(private session: SessionService, private router: Router, public dialog: MatDialog) { }

  ngOnInit() {
    this.uploader.onSuccessItem = (item, response) => {
      this.user = JSON.parse(response);
    };
    this.session.userLoggedIn()
      .subscribe(
      (user) => {
        this.user = user;
        this.personalInfo = user;
        this.session.getExperiences(this.user._id)
          .subscribe(
          (experiences) => {
            this.experiences = experiences;
          }
          )
        this.session.getCVs(this.user._id)
          .subscribe(
            (curriculums) => {
              this.curriculums = curriculums;
            }
          )
        this.session.getAcademics(this.user._id)
          .subscribe(
          (academics) => {
            this.academics = academics;
          }
          )
        this.session.getSkills(this.user._id)
          .subscribe(
          (skills) => {
            this.skills = skills;
          }
          )
        this.session.getHobbies(this.user._id)
          .subscribe(
          (hobbies) => {
            this.hobbies = hobbies;
          }
          )
        this.session.getCertifications(this.user._id)
          .subscribe(
          (certifications) => {
            this.certifications = certifications;
          }
          )
      }
      );
  }

  logout() {
    this.session.logout()
    .subscribe(
      (msg) => this.router.navigate(['/'])
    )
  }

  editProfile(id) {
    let dialogRef = this.dialog.open(EditProfileDialogComponent, {
      height: 'auto',
      width: '600px',
      data: {user: id}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.user = result;
      }
    });
  }

  showDashboard(e) {
    this.experienceContainer = false;
    this.academicContainer = false;
    this.skillContainer = false;
    this.hobbieContainer = false;
    this.certificateContainer = false;
    this.dashboardContainer = true;
  }

  showExperience(e) {
    this.academicContainer = false;
    this.skillContainer = false;
    this.hobbieContainer = false;
    this.certificateContainer = false;
    this.dashboardContainer = false;
    this.experienceContainer = true;
  }

  showAcademic(e) {
    this.skillContainer = false;
    this.hobbieContainer = false;
    this.certificateContainer = false;
    this.dashboardContainer = false;
    this.experienceContainer = false;
    this.academicContainer = true;
  }

  showCertificate(e) {
    this.academicContainer = false;
    this.skillContainer = false;
    this.hobbieContainer = false;
    this.dashboardContainer = false;
    this.experienceContainer = false;
    this.certificateContainer = true;
  }

  showSkill(e) {
    this.academicContainer = false;
    this.hobbieContainer = false;
    this.certificateContainer = false;
    this.dashboardContainer = false;
    this.experienceContainer = false;
    this.skillContainer = true;
  }

  showHobbie(e) {
    this.academicContainer = false;
    this.skillContainer = false;
    this.certificateContainer = false;
    this.dashboardContainer = false;
    this.experienceContainer = false;
    this.hobbieContainer = true;
  }


  createNew() {
    let personalInfo = this.personalInfo;
    let experiences = this.experiences;
    let academics = this.academics;
    let skills = this.skills;
    let hobbies = this.hobbies;
    let courses = this.certifications;
    let dialogRef = this.dialog.open(StepperBuilderComponent, {
      height: '90vh',
      width: '80%',
      data: {
        personalInfo: personalInfo,
        experiences: experiences,
        academics: academics,
        skills: skills,
        courses: courses,
        hobbies: hobbies
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.router.navigate(['/generate-cv/'+result.data])
      }
    });
  }

  imprimirElCV(){
    this.router.navigate(['/generate-cv']);
  }

  submit() {
    this.uploader.onBuildItemForm = (item, form) => {};
    this.uploader.uploadAll()

  }

  


}



