import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes, RouterModule } from "@angular/router";
import { FileUploadModule } from "ng2-file-upload";
import { BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { MatButtonModule, MatCheckboxModule, MatDialogModule, MatStepperModule, MatChipsModule, MatInputModule} from '@angular/material';
import { MatFormFieldModule, MatSelectModule, MatSlideToggleModule, MatSliderModule} from '@angular/material';

import { AppComponent } from './app.component';
import { SignInComponent } from './sign-in/sign-in.component';
import { SessionService } from './session.service';
import { SignUpComponent } from './sign-up/sign-up.component';
import { HomeComponent } from './home/home.component';
import { NavBarComponent } from './nav-bar/nav-bar.component';
import { PersonalInfoFormComponent } from './personal-info-form/personal-info-form.component';
import { ProfileComponent } from './profile/profile.component';
import { FooterComponent } from './footer/footer.component';
import { ExperiencesListComponent } from './experiences/experiences-list/experiences-list.component';
import { SkillsComponent } from './skills/skills.component';
import { ExtrasComponent } from './hobbies/list-hobbies/extras.component';
import { CertificationsComponent } from './certifications/list-certifications/extras.component';
import { MasonryModule } from 'angular2-masonry';
import { GeneratorComponent } from './generator/generator.component';
import { AcademicsComponent } from './academics/academics-list/academics.component';
import { AddExperienceDialogComponent } from './experiences/add-experience-dialog/add-experience-dialog.component';
import { EditExperienceDialogComponent } from './experiences/edit-experience-dialog/edit-experience-dialog.component';
import { AddAcademicsDialogComponent } from './academics/add-academics-dialog/add-academics-dialog.component';
import { EditAcademicsDialogComponent } from './academics/edit-academics-dialog/edit-academics-dialog.component';
import { AddSkillsComponent } from './skills/add-skills/add-skills.component';
import { EditSkillsComponent } from './skills/edit-skills/edit-skills.component';
import { AddCertificationsComponent } from './certifications/add-certifications/add-certifications.component';
import { EditCertificationsComponent } from './certifications/edit-certifications/edit-certifications.component';
import { EditProfileDialogComponent } from './edit-profile-dialog/edit-profile-dialog.component';
import { StepperBuilderComponent } from './stepper-builder/stepper-builder.component';
import { SelectDesignComponent } from './select-design/select-design.component';
import { SelectExperiencesComponent } from './select-experiences/select-experiences.component';
import { SelectAcademicsComponent } from './select-academics/select-academics.component';
import { SelectSkillsComponent } from './select-skills/select-skills.component';
import { SelectCoursesHobbiesComponent } from './select-courses-hobbies/select-courses-hobbies.component';
import { SelectResumeeComponent } from './select-resumee/select-resumee.component';
import { SelectHobbiesComponent } from './select-hobbies/select-hobbies.component';
import { LogPopUpComponent } from './log-pop-up/log-pop-up.component';
import { SaveMsgComponent } from './save-msg/save-msg.component';
import { Design1Component } from './designs/design-1/design-1.component';
import { Design2Component } from './designs/design-2/design-2/design-2.component';
import { Design3Component } from './designs/design-3/design-3/design-3.component';
import { HowWorksComponent } from './how-works/how-works.component';
import { ContactComponent } from './contact/contact.component';
import { SendMessageComponent } from './send-message/send-message.component';


const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home',  component: HomeComponent },
  { path: 'signup',  component: SignUpComponent },
  { path: 'signin', component: SignInComponent },
  { path: 'how-works', component: HowWorksComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'personal-info', component: PersonalInfoFormComponent },
  { path: 'generate-cv/:id', component: GeneratorComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    FileUploadModule,
    SignInComponent,
    SignUpComponent,
    HomeComponent,
    NavBarComponent,
    PersonalInfoFormComponent,
    ProfileComponent,
    FooterComponent,
    ExperiencesListComponent,
    SkillsComponent,
    ExtrasComponent,
    GeneratorComponent,
    AcademicsComponent,
    AddExperienceDialogComponent,
    EditExperienceDialogComponent,
    AddAcademicsDialogComponent,
    EditAcademicsDialogComponent,
    AddSkillsComponent,
    EditSkillsComponent,
    AddCertificationsComponent,
    EditCertificationsComponent,
    CertificationsComponent,
    EditProfileDialogComponent,
    StepperBuilderComponent,
    SelectDesignComponent,
    SelectExperiencesComponent,
    SelectAcademicsComponent,
    SelectSkillsComponent,
    SelectCoursesHobbiesComponent,
    SelectResumeeComponent,
    SelectHobbiesComponent,
    LogPopUpComponent,
    SaveMsgComponent,
    Design1Component,
    Design2Component,
    Design3Component,
    HowWorksComponent,
    ContactComponent,
    SendMessageComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    RouterModule.forRoot(routes),
    MasonryModule,
    BrowserAnimationsModule,
    MatButtonModule, MatSlideToggleModule, MatSliderModule, MatSelectModule, MatCheckboxModule, MatDialogModule, MatStepperModule, MatChipsModule, MatInputModule
  ],
  entryComponents: [
    AddExperienceDialogComponent,
    EditExperienceDialogComponent,
    AddAcademicsDialogComponent,
    EditAcademicsDialogComponent,
    AddSkillsComponent,
    EditSkillsComponent,
    AddCertificationsComponent,
    EditCertificationsComponent,
    EditProfileDialogComponent,
    StepperBuilderComponent,
    LogPopUpComponent,
    SaveMsgComponent,
    SendMessageComponent
  ],
  providers: [ SessionService ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



