import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-select-experiences',
  templateUrl: './select-experiences.component.html',
  styleUrls: ['./select-experiences.component.css']
})
export class SelectExperiencesComponent implements OnInit {
  @Input() experiences: any;
  @Output() onExperiences = new EventEmitter<any>();
  experienceSelected = [];
  constructor() { }

  ngOnInit() {
  }

  toggleExperience(e, experience) {
    if(!e.target.classList.contains("active")) {
      e.target.classList.add("active");
      e.target.classList.add("z-depth-1"); 
      this.experienceSelected.push(experience);
    } else {
      e.target.classList.remove("active");
      e.target.classList.remove("z-depth-1");
      this.experienceSelected = this.experienceSelected.filter(e => e._id !== experience._id);
    }
    this.onExperiences.emit(this.experienceSelected);
  }

}



