import { Component, OnInit } from '@angular/core';
import { LogPopUpComponent } from '../log-pop-up/log-pop-up.component';
import {  MatDialog } from '@angular/material';
import { Router } from '@angular/router';
import { SessionService } from '../session.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  user;
  showMobileMenu = false;
  constructor(public dialog: MatDialog,  private router: Router, private session: SessionService) { }

  ngOnInit() {
    this.session.isLoggedIn()
    .subscribe(user => {
      this.user = user;
    });
  };

  logout() {
    this.session.logout()
    .subscribe(
      (msg) => {
        this.user = null;
      });
  }
  
  signIn(type){
    let dialogRef = this.dialog.open(LogPopUpComponent, {
      height: 'auto',
      width: 'auto',
      data: {
        type: type
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        if (result == "successLogin") this.router.navigate(['/profile'])
        if (result == "successSignUp") this.router.navigate(['/personal-info'])
      }
    });
  }

  showMenu() {
    this.showMobileMenu = !this.showMobileMenu;
  }
}



