import { Component, OnInit } from '@angular/core';
import { SessionService } from '../session.service';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { SendMessageComponent } from '../send-message/send-message.component';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  name;
  email;
  message;
  constructor(private session: SessionService, public dialog: MatDialog, private router: Router) { }

  ngOnInit() {
  }

  sendMessage() {
    this.session.sendMessage({name: this.name, email: this.email, message: this.message})
    .subscribe((message) => {
      const dialogRef = this.dialog.open(SendMessageComponent, {
        height: 'auto'
      });
      this.router.navigate(['/home'])
    });
    
  }
}



