import { Component, OnInit } from '@angular/core';
import { SessionService } from '../../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-add-certifications',
  templateUrl: './add-certifications.component.html',
  styleUrls: ['./add-certifications.component.css']
})
export class AddCertificationsComponent implements OnInit {
  certificationForm = {
    userId: '',
    name: '',
    entity: '',
    year:''
  }
  error: any;
  constructor(private session: SessionService, public certificationAddRef: MatDialogRef<AddCertificationsComponent>) { }

  ngOnInit() {
  }

  addCertification() {
    this.session.addCertification(this.certificationForm)
      .subscribe(
        (extra) => this.successCertificationCb(extra),
        (err) => this.errorCb(err)
      );
  }

  errorCb(err) {
    this.error = err;
  }


  successCertificationCb(certification) {
    this.certificationAddRef.close(certification)
    this.error = null;
  }
}



