import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { SessionService } from '../session.service';
import { FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';

@Component({
  selector: 'app-personal-info-form',
  templateUrl: './personal-info-form.component.html',
  styleUrls: ['./personal-info-form.component.css']
})
export class PersonalInfoFormComponent implements OnInit {
  form: FormGroup;
  formInfo = {
    id: '',
    name: '',
    lastName: '',
    city: '',
    country: '',
    phone: '',
    email: '',
    linkedin: '',
    web: '',
    profession: ''
  };
  user: any;
  subscription: any;
  error: any;


  constructor(private session: SessionService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.session.userLoggedIn()
      .subscribe(
        (user) => {
          console.log(user)
          this.user = user
        }
      );
  }

  submit() {
    if (this.formInfo.name == '' ||
        this.formInfo.lastName == '' ||
        this.formInfo.city == '' ||
        this.formInfo.country == '' ||
        this.formInfo.phone == '' ||
        this.formInfo.email == '' ||
        this.formInfo.linkedin == '' ||
        this.formInfo.profession == '') {
          this.error = "Debes completar todos los campos!"
    } else {
      this.formInfo.id = this.user._id;
      this.session.completeInfo(this.formInfo)
        .subscribe(
          (user) => this.successCb(user),
          (err) => this.errorCb(err)
        );
    } 
  }

  errorCb(err) {
    this.error = err;
    this.user = null;
  }

  successCb(user) {
    this.user = user;
    this.error = null;
    this.router.navigate(['/profile']);
  }

}



