import { Component, OnInit, Inject } from '@angular/core';
import { SessionService } from '../../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-edit-experience-dialog',
  templateUrl: './edit-experience-dialog.component.html',
  styleUrls: ['./edit-experience-dialog.component.css']
})
export class EditExperienceDialogComponent implements OnInit {
  formInfo = {
    experienceId: '',
    company: '',
    userId: '',
    startDate: '',
    endDate: '',
    jobTitle: '',
    city: '',
    country: '',
    jobDescription: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = false;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.","Mar.","Abr.","May.","Jun.","Jul.","Ago.","Sep.","Oct.", "Nov.","Dic."]
  selectOptionsYears = ["2017", "2016","2015","2014","2013","2012","2011","2010","2009","2008", "2007","2006", "2005","2004","2003","2002","2001","2000"]
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private session: SessionService, public experienceEditRef: MatDialogRef<EditExperienceDialogComponent>) { }
  ngOnInit() {
    this.formInfo.experienceId = this.data.experience;
    this.session.getExperience(this.data.experience)
    .subscribe(experience => {
      this.formInfo.company = experience[0].company;
      this.formInfo.city = experience[0].city;
      this.formInfo.country = experience[0].country;
      this.formInfo.jobTitle = experience[0].jobTitle;
      this.formInfo.jobDescription = experience[0].jobDescription;
      this.startMonth = experience[0].startDate.substring(0,4)
      this.startYear = "20"+experience[0].startDate.substring(6,8)
      if(experience[0].endDate !== "") {
        this.endDate = true;
        this.endMonth = experience[0].endDate.substring(0,4)
        this.endYear = "20"+experience[0].endDate.substring(6,8)
      }
    })
  }

  toggleEndDate() {
    this.endDate = !this.endDate;
  }

  editExperience() {
    this.formInfo.startDate = this.startMonth+" '"+this.startYear.slice(2,4);
    this.endMonth !== undefined ? this.formInfo.endDate = this.endMonth+" '"+this.endYear.slice(2,4) : this.formInfo.endDate = '' ;
    this.session.editExperience(this.formInfo)
      .subscribe(experience => {
          this.experienceEditRef.close(experience)
      })
  }

}



