import { Component, OnInit } from '@angular/core';
import { SessionService } from '../../session.service';
import {MatDialog, MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-add-academics-dialog',
  templateUrl: './add-academics-dialog.component.html',
  styleUrls: ['./add-academics-dialog.component.css']
})
export class AddAcademicsDialogComponent implements OnInit {
  formInfo = {
    institute: '',
    userId: '',
    startDate: '',
    endDate: '',
    degreeTitle: '',
    degreeLevel: '',
    city: '',
    country: '',
    description: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = false;
  showForm:boolean = false;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.","Mar.","Abr.","May.","Jun.","Jul.","Ago.","Sep.","Oct.", "Nov.","Dic."]
  selectOptionsYears = ["2017", "2016","2015","2014","2013","2012","2011","2010","2009","2008", "2007","2006", "2005","2004","2003","2002","2001","2000"]
  constructor(private session: SessionService, public academicAddRef: MatDialogRef<AddAcademicsDialogComponent>) { }

  ngOnInit() {
  }
  addAcademics() {
    this.formInfo.startDate = this.startMonth+" '"+this.startYear.slice(2,4);
    this.endMonth !== undefined ? this.formInfo.endDate = this.endMonth+" '"+this.endYear.slice(2,4) : this.formInfo.endDate = '' ;
    this.session.addAcademics(this.formInfo)
      .subscribe(
        (academic) => this.successCb(academic),
        (err) => this.errorCb(err)
      );

  }

  errorCb(err) {
    this.error = err;
    this.user = null;
  }

  successCb(academic) {
    this.error = null;
    this.academicAddRef.close(academic)
  }

  toggleEndDate() {
    this.endDate = !this.endDate;
  }

}



