import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SessionService } from '../session.service';

@Component({
  selector: 'app-select-design',
  templateUrl: './select-design.component.html',
  styleUrls: ['./select-design.component.css']
})
export class SelectDesignComponent implements OnInit {
  @Input() data: any;
  @Output() onDesign = new EventEmitter<any>();
  designs: any;
  design;
  constructor(private session: SessionService) { }

  ngOnInit() {
    this.session.getDesigns()
    .subscribe(
    (designs) => {
      this.designs = designs;
    }
    );
  }

  toggleDesign(e, design) {
    this.design = design;
    let designs = document.getElementsByClassName("item-container");
    for (let i=0 ; i < designs.length; i++) {
      designs[i].classList.remove("active")
    };
    e.target.classList.add("active");
    this.onDesign.emit(this.design);
  }


}



