import { Component, OnInit, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-log-pop-up',
  templateUrl: './log-pop-up.component.html',
  styleUrls: ['./log-pop-up.component.css']
})
export class LogPopUpComponent implements OnInit {
  login: boolean = false;
  signUp: boolean = false;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any, public logPopUp: MatDialogRef<LogPopUpComponent> ) { }

  ngOnInit() {
    this.checkType(this.data.type);
  }

  loginCheck(typeCheck) {
    if(typeCheck == "login" || typeCheck == "signUp") this.checkType(typeCheck);
    if (typeCheck == "successLogin" || typeCheck == "successSignUp")  this.successCb(typeCheck)
  }

  checkType(type){
    if (type == "login") {
      this.login = true;
      this.signUp = false;
    }
    if (type == "signUp") {
      this.signUp = true;
      this.login = false;
    }
  }

  successCb(successType){
    this.logPopUp.close(successType)
  }
  

}



