import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-select-academics',
  templateUrl: './select-academics.component.html',
  styleUrls: ['./select-academics.component.css']
})
export class SelectAcademicsComponent implements OnInit {
  @Input() academics: any;
  @Output() onAcademics = new EventEmitter<any>();
  academicsSelected = [];
  constructor() { }

  ngOnInit() {
  }

  toggleAcademics(e, academics) {
    if(!e.target.classList.contains("active")) {
      e.target.classList.add("active");
      e.target.classList.add("z-depth-1"); 
      this.academicsSelected.push(academics);
    } else {
      e.target.classList.remove("active");
      e.target.classList.remove("z-depth-1");
      this.academicsSelected = this.academicsSelected.filter(e => e._id !== academics._id);
    }
    this.onAcademics.emit(this.academicsSelected);
  }
}



