import { Component, OnInit, Input} from '@angular/core';

@Component({
  selector: 'app-design-1',
  templateUrl: './design-1.component.html',
  styleUrls: ['./design-1.component.css']
})
export class Design1Component implements OnInit {
@Input() currentColor:any;
@Input() cv:any;
  constructor() { }

  ngOnInit() {
    this.currentColor = this.cv.color;
  }

  getStyles(skill) {
    return {
      'width': skill.level+'%',
      'backgroundColor': this.currentColor
    }
  }

}



