import { Component, OnInit, Input } from '@angular/core';
import { SessionService } from '../../session.service';
import { ActivatedRoute, Router } from '@angular/router'
import {MatDialog} from '@angular/material';
import {AddExperienceDialogComponent} from '../add-experience-dialog/add-experience-dialog.component'
import {EditExperienceDialogComponent} from '../edit-experience-dialog/edit-experience-dialog.component'

@Component({
  selector: 'app-experiences-list',
  templateUrl: './experiences-list.component.html',
  styleUrls: ['./experiences-list.component.css']
})
export class ExperiencesListComponent implements OnInit {
  @Input() experiences: any;
  birthdate;
  formInfo = {
    company: '',
    userId: '',
    startDate: '',
    endDate: '',
    jobPosition: '',
    city: '',
    country: '',
    jobDescription: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = false;
  showForm: boolean = false;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.", "Mar.", "Abr.", "May.", "Jun.", "Jul.", "Ago.", "Sep.", "Oct.", "Nov.", "Dic."]
  selectOptionsYears = ["2017", "2016", "2015", "2014", "2013", "2012", "2011", "2010", "2009", "2008", "2007", "2006", "2005", "2004", "2003", "2002", "2001", "2000"]

  constructor(private session: SessionService, private route: ActivatedRoute, private router: Router, public dialog: MatDialog) {
  }
  ngOnInit() {
    
  }

  toggleForm() {
    let dialogRef = this.dialog.open(AddExperienceDialogComponent, {
      height: 'auto',
      width: '600px',

    });
    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.experiences.unshift(result)
      }
    });
  }

  errorCb(err) {
    this.error = err;
    this.user = null;
  }


  remove(id, event) {
    this.session.deleteExperience(id)
      .subscribe(
      (experience) => {
        this.experiences = this.experiences.filter(e => e._id !== id);
      },
      (err) => this.errorCb(err)
      );
  }

  edit(id) {
    let editDialogRef = this.dialog.open(EditExperienceDialogComponent, {
      height: 'auto',
      width: '600px',
      data: {experience: id}
    });
    editDialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.experiences = this.experiences.filter(e => e._id !== result._id);
        this.experiences.unshift(result)
      }
    });
  }



}



