import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MatDialogRef} from '@angular/material';
import {MAT_DIALOG_DATA} from '@angular/material';
import { SessionService } from '../../session.service';

@Component({
  selector: 'app-edit-academics-dialog',
  templateUrl: './edit-academics-dialog.component.html',
  styleUrls: ['./edit-academics-dialog.component.css']
})
export class EditAcademicsDialogComponent implements OnInit {
  formInfo = {
    academicsId:'',
    institute: '',
    userId: '',
    startDate: '',
    endDate: '',
    degreeTitle: '',
    degreeLevel: '',
    city: '',
    country: '',
    description: ''
  };
  endYear: any;
  endMonth: any;
  startYear: any;
  startMonth: any;
  endDate: boolean = false;
  showForm:boolean = false;
  error: any;
  user: any;
  selectOptionsMonths = ["Ene.", "Feb.","Mar.","Abr.","May.","Jun.","Jul.","Ago.","Sep.","Oct.", "Nov.","Dic."]
  selectOptionsYears = ["2017", "2016","2015","2014","2013","2012","2011","2010","2009","2008", "2007","2006", "2005","2004","2003","2002","2001","2000"]
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private session: SessionService, public academicEditRef: MatDialogRef<EditAcademicsDialogComponent>) { }

  ngOnInit() {
    this.formInfo.academicsId = this.data.academic;
    this.session.getAcademic(this.data.academic)
    .subscribe(academic => {
      this.formInfo.institute = academic[0].institute;
      this.formInfo.city = academic[0].city;
      this.formInfo.country = academic[0].country;
      this.formInfo.degreeTitle = academic[0].degreeTitle;
      this.formInfo.degreeLevel = academic[0].degreeLevel;
      this.formInfo.description = academic[0].description;
      this.startMonth = academic[0].startDate.substring(0,4)
      this.startYear = "20"+academic[0].startDate.substring(6,8);
      console.log(academic[0])
      if(academic[0].endDate !== "") {
        this.endDate = true;
        this.endMonth = academic[0].endDate.substring(0,4)
        this.endYear = "20"+academic[0].endDate.substring(6,8)
      }
    })
  }

  editAcademics() {
    this.formInfo.startDate = this.startMonth+" '"+this.startYear.slice(2,4);
    this.endMonth !== undefined ? this.formInfo.endDate = this.endMonth+" '"+this.endYear.slice(2,4) : this.formInfo.endDate = '' ;
    this.session.editAcademic(this.formInfo)
      .subscribe(academic => {
          this.academicEditRef.close(academic)
      })
  }

}



