import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators, ValidatorFn } from '@angular/forms';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-stepper-builder',
  templateUrl: './stepper-builder.component.html',
  styleUrls: ['./stepper-builder.component.css']
})
export class StepperBuilderComponent implements OnInit {
  generateCV = {
    design: {
      experiences: 0,
      academics: 0,
      skills: 0,
      courses: 0,
      hobbies: 0
    },
    personalInfo: {},
    experiences: [],
    academics: [],
    skills: [],
    hobbies: [],
    certificates: []
  };
  design;
  experiences;
  academics;
  skills;
  courses;
  hobbies;
  firstFormGroup: FormGroup;
  experiencesFormGroup: FormGroup;
  academicsFormGroup: FormGroup;
  skillsFormGroup: FormGroup;
  coursesFormGroup: FormGroup;
  hobbiesFormGroup: FormGroup;
  isLinear = true;
  formGroup = false;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private _formBuilder: FormBuilder) { }

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.min(0)]
    });
    this.experiencesFormGroup = this._formBuilder.group({
      experiencesCtrl: ['', [Validators.min(this.generateCV.design.experiences), Validators.max(this.generateCV.design.experiences)]]
    });
    this.academicsFormGroup = this._formBuilder.group({
      academicsCtrl: ['', [Validators.min(this.generateCV.design.academics), Validators.max(this.generateCV.design.academics)]]
    });
    this.skillsFormGroup = this._formBuilder.group({
      skillsCtrl: ['', [Validators.min(this.generateCV.design.skills), Validators.max(this.generateCV.design.skills)]]
    });
    this.coursesFormGroup = this._formBuilder.group({
      coursesCtrl: ['', [Validators.min(this.generateCV.design.courses), Validators.max(this.generateCV.design.courses)]]
    });
    this.hobbiesFormGroup = this._formBuilder.group({
      hobbiesCtrl: ['', [Validators.min(this.generateCV.design.hobbies), Validators.max(this.generateCV.design.hobbies)]]
    });
    this.generateCV.personalInfo = this.data.personalInfo;
  }
  addDesign(design) {
    this.generateCV.design = design;
    if (this.generateCV.design !== {}) {
      this.design = 1;
    } else {
      this.design = 0;
    }
    this.experiencesFormGroup = this._formBuilder.group({
      experiencesCtrl: ['', [Validators.min(this.generateCV.design.experiences), Validators.max(this.generateCV.design.experiences)]]
    });
    this.academicsFormGroup = this._formBuilder.group({
      academicsCtrl: ['', [Validators.min(this.generateCV.design.academics), Validators.max(this.generateCV.design.academics)]]
    });
    this.skillsFormGroup = this._formBuilder.group({
      skillsCtrl: ['', [Validators.min(this.generateCV.design.skills -1), Validators.max(this.generateCV.design.skills)]]
    });
    this.coursesFormGroup = this._formBuilder.group({
      coursesCtrl: ['', [Validators.min(this.generateCV.design.courses), Validators.max(this.generateCV.design.courses)]]
    });
    this.hobbiesFormGroup = this._formBuilder.group({
      hobbiesCtrl: ['', [Validators.min(this.generateCV.design.hobbies), Validators.max(this.generateCV.design.hobbies)]]
    });
  }
  addExperiences(experiences) {
    this.generateCV.experiences = experiences;
    this.experiences = this.generateCV.experiences.length;
  }
  addAcademics(academics) {
    this.generateCV.academics = academics;
    this.academics = this.generateCV.academics.length;
  }
  addSkills(skills) {
    this.generateCV.skills = skills;
    this.skills = this.generateCV.skills.length;
  }
  addCourses(courses) {
    this.generateCV.certificates = courses;
    this.courses = this.generateCV.certificates.length;
  }
  addHobbies(hobbies) {
    this.generateCV.hobbies = hobbies;
    this.hobbies = this.generateCV.hobbies.length;
  }

}



