import { Component, OnInit, Input } from '@angular/core';
import { SessionService } from '../../session.service'
import { ActivatedRoute } from '@angular/router'
@Component({
  selector: 'app-extras',
  templateUrl: './extras.component.html',
  styleUrls: ['./extras.component.css']
})
export class ExtrasComponent implements OnInit {
  @Input() hobbies: any;
  hobbieForm = {
    name: ''
  }
  error: any;

  constructor(private session: SessionService, private route: ActivatedRoute) { }

  ngOnInit() {

  }

  addHobbie() {
    console.log(this.hobbieForm)
    this.session.addHobbie(this.hobbieForm)
      .subscribe(
        (hobbie) => this.successHobbieCb(hobbie),
        (err) => this.errorCb(err)
      );
  }

  errorCb(err) {
    this.error = err;
    this.hobbies = null;
  }

  successHobbieCb(hobbie) {
    console.log(hobbie)
    this.hobbies.unshift(hobbie);
    this.hobbieForm.name = "";
    this.error = null;
  }

  removeHobbie(id, event){
    this.session.deleteHobbie(id)
      .subscribe(
        (hobbie) => {
          this.hobbies = this.hobbies.filter(e => {
            return e._id !== id;
          })
        },
        (err) => this.errorCb(err)
      );
  }

}



