import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-select-hobbies',
  templateUrl: './select-hobbies.component.html',
  styleUrls: ['./select-hobbies.component.css']
})
export class SelectHobbiesComponent implements OnInit {
  @Input() hobbies: any;
  @Output() onHobbies = new EventEmitter<any>();
  hobbiesSelected = [];
  constructor() { }

  ngOnInit() {
  }
  toggleHobbies(e, hobbies) {
    if(!e.target.classList.contains("active")) {
      e.target.classList.add("active");
      this.hobbiesSelected.push(hobbies);
    } else {
      e.target.classList.remove("active");
      this.hobbiesSelected = this.hobbiesSelected.filter(e => e._id !== hobbies._id);
    }
    this.onHobbies.emit(this.hobbiesSelected);
  }

}



