import { Component, OnInit, Input} from '@angular/core';

@Component({
  selector: 'app-design-3',
  templateUrl: './design-3.component.html',
  styleUrls: ['./design-3.component.css']
})
export class Design3Component implements OnInit {
  @Input() currentColor:any;
  @Input() cv:any;
  constructor() { }

  ngOnInit() {
    this.currentColor = this.cv.color;
  }

}



