import { Component, OnInit, Input } from '@angular/core';
import { SessionService } from '../session.service';
import { Router } from '@angular/router';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-select-resumee',
  templateUrl: './select-resumee.component.html',
  styleUrls: ['./select-resumee.component.css']
})
export class SelectResumeeComponent implements OnInit {
  @Input() curriculum: any;
  constructor(private session: SessionService, private router: Router, public stepperDialog: MatDialogRef<SelectResumeeComponent>) { }

  ngOnInit() {
  }

  guardarCV(){
    this.session.addCV(this.curriculum)
    .subscribe(res => {
      this.stepperDialog.close({data: res._id});
    })
  }

}



